.container {
  background: url("../img/main/bg.jpg");
  background-size: cover;
  position: relative; }

.container_no-bg {
  background: none; }

.container__fixed {
  max-width: 1140px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  @include desktopBig {
    max-width: 1360px; }
  @include desktopLarge {
    max-width: 1535px; }
  @include desktopSmall {
    max-width: 728px; } }

.layout {
  display: flex;
  justify-content: space-between; }

.container__heading {
  @include fontFSC($goth-b, 48px);
  text-align: center;
  margin-top: 52px;
  line-height: 50px;
  @include desktopSmall {
    font-size: 36px; } }

.container__title {
  @include fontFSC($goth-bl, 36px);
  text-align: left;
  text-transform: uppercase;
  line-height: 34px;
  margin-top: 45px; }

.container__location {
  @include fontFSC($goth-r, 14px, $clr);
  text-align: left;
  line-height: 20px;
  cursor: pointer; }

.container__back {
  @include fontFSC($goth-r, 14px, $clr);
  line-height: 20px;
  margin-top: 22px;
  cursor: pointer;
  &:before {
    content: url("../img/main/back.svg");
    display: inline-block;
    height: 15px;
    margin-right: 16px;
    vertical-align: sub; }
  .container__back-text {
    display: inline-block; } }

.container__img {
  width: 100%; }

.button {
  display: block;
  border-radius: 20px;
  background-image: url("data:image/svg+xml,%3Csvg width='215' height='34' viewBox='0 0 215 34' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='1' y='1' width='213' height='32' rx='16' stroke='url(%23paint0_linear)' stroke-width='2'/%3E%3Cdefs%3E%3ClinearGradient id='paint0_linear' x1='1.64541' y1='17' x2='229.236' y2='21.0341' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='%23E5E5E5'/%3E%3Cstop offset='1' stop-color='%23E5E5E5' stop-opacity='0'/%3E%3C/linearGradient%3E%3C/defs%3E%3C/svg%3E");
  background-color: transparent;
  border: none;
  height: 34px;
  width: 215px;
  text-align: center;
  line-height: 34px;
  @include fontFSC($goth-m, 16px, $orange);
  text-transform: uppercase;
  box-sizing: border-box;
  padding: 0;
  cursor: pointer;
  transition: all 200ms ease-in-out;
  &:hover {
    background: $orange;
    @include fontFSC($goth-m, 16px); } }

.button_long {
  background-image: url("data:image/svg+xml,%3Csvg width='262' height='34' viewBox='0 0 262 34' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='1' y='1' width='260' height='32' rx='16' stroke='url(%23paint0_linear)' stroke-width='2'/%3E%3Cdefs%3E%3ClinearGradient id='paint0_linear' x1='2.0051' y1='17' x2='279.306' y2='22.9897' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='%23E5E5E5'/%3E%3Cstop offset='1' stop-color='%23E5E5E5' stop-opacity='0'/%3E%3C/linearGradient%3E%3C/defs%3E%3C/svg%3E");
  background-color: transparent;
  width: 262px;
  &:hover {
    width: 262px; } }

.button-org {
  display: block;
  background-color: $orange;
  border: none;
  height: 35px;
  width: 185px;
  text-align: center;
  line-height: 35px;
  @include fontFSC($goth-m, 14px);
  text-transform: uppercase;
  box-sizing: border-box;
  padding: 0;
  cursor: pointer;
  transition: all 200ms ease-in-out;
  &:hover {
    background: #CD070F; } }

.input-field {
  display: block;
  height: 55px;
  width: 367px;
  box-sizing: border-box;
  padding: 18px 24px;
  background-color: transparent;
  border: 1px solid #C1BBBB;
  outline: none;
  @include fontFSC($goth-r, 19px);
  &:focus {
    border: 1px solid $orange; } }

.input-field_area {
  height: 127px;
  resize: none; }

.link {
  @include fontFSC($goth-m, 14px, $clr);
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  div {
    transition: all 0.2s ease-in-out; }
  svg {
    transition: all 0.2s ease-in-out;
    path {
      transition: all 0.2s ease-in-out; } }
  &:hover {
    color: $orange;
    div {
      color: $orange; }
    svg {
      fill: $orange;
      path {
        fill: $orange; } } } }

.radio-wrap {
  display: block;
  position: relative;
  padding-left: 40px;
  padding-right: 20px;
  margin-bottom: 12px;
  @include fontFSC($goth-r, 16px);
  line-height: 25px;
  cursor: pointer;
  user-select: none;
  .radio {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    &:checked ~ .radio-label {
      &::after {
        display: block; } } }
  .radio-label {
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    background-color: #fff;
    border-radius: 50%;
    border: 2px solid #DCE5E5;
    box-sizing: border-box;
    &::after {
      content: "";
      position: absolute;
      display: none;
      top: 3px;
      left: 3px;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background: $orange; } } }

.check-wrap {
  display: block;
  position: relative;
  padding-left: 40px;
  padding-right: 20px;
  margin-bottom: 12px;
  @include fontFSC($goth-r, 16px);
  line-height: 25px;
  cursor: pointer;
  user-select: none;
  .check {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    &:checked ~ .check-label {
      &::after {
        display: block; } } }
  .check-label {
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    background-color: transparent;
    border: 1px solid #fff;
    box-sizing: border-box;
    &::after {
      content: url('../img/checkout/check.svg');
      position: absolute;
      fill: $orange;
      display: none;
      top: -2px;
      left: 3px;
      width: 20px;
      height: 15px; } } }

.select-wrap {
  position: relative;
  cursor: pointer;
  svg {
    position: absolute;
    top: 13px;
    right: 9px; } }

.select {
  height: 34px;
  width: 124px;
  padding: 9px 10px;
  box-sizing: border-box;
  border: none;
  border-radius: 0;
  background-color: #646161;
  outline: none;
  appearance: none;
  @include fontFSC($goth-r, 14px);
  line-height: 16px; }

.menu-button {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 35px;
  height: 25px;
  background-color: transparent;
  span {
    transition: all 0.2s ease-in-out; }
  &:hover {
    span {
      background-color: $orange; } } }

.menu-button__line {
  display: block;
  height: 3px;
  border-radius: 2px;
  width: 100%;
  background-color: #fff; }
